.about{
	.b-list{
		li{
			&+li{
				margin-top: 1rem;
			}
		}
	}

	&__contacts{
		background-color: white;
		border-radius: 2px;
		box-shadow: 0 1px 3px rgba(211, 211, 211, 0.8);
		border-top: 6/@rem solid @cGold;
		padding: 27/@rem 41/@rem 54/@rem;
		&-title{
			font-size: 20/@rem;
			font-weight: 700;
			line-height: 3rem;
			margin-bottom: 39/@rem;
		}
		&-block{
			font-size: 16/@rem;
			line-height: 25/@rem;
			margin-top: 35/@rem;
			max-width: 28rem;
			&-title{
				font-weight: 300;
				color: @title-color;
			}
			&-text{
				font-weight: 600;
				margin-top: 10/@rem;
				a{
					.color(@text-color);
				}
			}
		}
	}
	&__rating{
		padding-top: 7.6rem;
		padding-bottom: 2.4rem;
		&-title{
			font-size: 60/@rem;
			font-weight: 700;
			letter-spacing: -1/@rem;
			line-height: 60/@rem;
			&:after{
				content: '';
				width: 2.7rem;
				height: 2rem;
				background: url('@{pathToImg}icons/rating.svg') no-repeat center;
				background-size: contain;
				display: inline-block;
				vertical-align: top;
				margin-left: 12/@rem;
				margin-top: 8/@rem;
			}
		}
		&-text{
			font-size: 28/@rem;
			font-weight: 300;
			letter-spacing: -0.39/@rem;
			line-height: 35/@rem;
			margin-top: 29/@rem;
		}
		h2{
			text-align: left;
			margin-bottom: 2.8rem;
		}
		h3{
			margin-bottom: 3.4rem;
		}
		&-items{
			.clearfix;
			&._media{
				.about__rating-item{
					height: 26.6rem;
				}
			}
		}
		.b-list{
			margin-top: 2.7rem;
			font-size: 14/@rem;
		}
		&-item{
			float: left;
			display: block;
			.color(@title-color);
			margin-bottom: 3rem;
			padding: 4.9rem 5.8rem;
			&._title{
				border-color: transparent;
				padding-top: 97/@rem;
				padding-left: 49/@rem;
			}
			&:nth-child(2n){
				margin-left: 3rem;
			}
			p{
				margin-top: 1.8rem;
				&.indent{
					margin-top: 6.7rem;
				}
			}
			width: 570/@rem;
			height: 337/@rem;
			border: 1px solid #dadada;
			border-radius: 2/@rem;
			&-title{
				font-size: 20/@rem;
				font-weight: 300;
				line-height: 25/@rem;
				color: @text-color;
			}
			&-numerals{
				margin-top: 23/@rem;
				&-items{
					.clearfix;
				}
				&-item{
					float: left;
					width: 50%;
					&._long{
						width: 70%;
					}
					&-val{
						font-size: 60/@rem;
						letter-spacing: -1/@rem;
						color: #3e3e3e;
						font-weight: 700;
						line-height: 60/@rem
					}
					&-text{
						font-size: 14/@rem;
						font-weight: 400;
						line-height: 24/@rem;
						color: @cGray;
						padding-right: 0.5rem;
						&:before{
							content: '';
							width: 7rem;
							height: 2/@rem;
							background-color: @cGold;
							margin-top: 2.1rem;
							margin-bottom: 1.8rem;
							display: block;
						}
					}
				}
			}
		}
	}
	&__history{
		overflow: hidden;
		.nav__btn{
			position: absolute;
			top: 7rem;
			&._prev{
				left: 0;
			}
			&._next{
				right: 0;
			}
		}
		&-in{
			padding-top: 10/@rem;
		}
		&-events{
			margin-top: 4.9rem;
			position: relative;
			&-gall{
				margin-left: 10rem;
				margin-right: 10rem;
			}
		}
		&-event{
			background-color: #fff;
			font-size: 16/@rem;
			line-height: 25/@rem;
			padding: 4.1rem 7rem 5rem;
			&-date{
				font-weight: 700;
			}
			&-text{
				margin-top: 0.8rem;
			}
		}
		&-scale{
			position: relative;
			padding-top: 5rem;
			cursor: move;
			&:after{
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				z-index: 3;
				width: 100%;
				height: 100%;
			}
			&-point{
				width: 25/@rem;
				height: 25/@rem;
				background-color: #cca266;
				border: 6/@rem solid white;
				box-shadow: 0 2/@rem 10/@rem rgba(23, 24, 26, 0.2);
				position: absolute;
				border-radius: 50%;
				bottom: 0;
				margin-bottom: -1.2rem;
				margin-left: -1.2rem;
				z-index: 5;
				cursor: pointer;
				&._active{
					background-color: @cRed;
				}
			}
			&-block{
				white-space: nowrap;

			}
			&-year{
				display: inline-block;
				vertical-align: top;
				width: 121.2rem;
				border-bottom: 1px solid @cGold;
				padding-bottom: 10/@rem;
				position: relative;
				&-title{
					font-size: 34/@rem;
					font-weight: 700;
					letter-spacing: -0.47/@rem;
					line-height: 4rem;
					position: absolute;
					width: 10rem;
					left: -5rem;
					bottom:100%;
					padding-bottom: 0.9rem;
					text-align: center;
				}
			}
			&-month{
				width: 10.1rem;
				float: left;
				position: relative;
				&-title{
					font-size: 12/@rem;
					font-weight: 600;
					letter-spacing: -0.17/@rem;
					text-transform: uppercase;
					color: #999;
					line-height: 2rem;
					position: absolute;
					left: -2rem;
					width: 4rem;
					bottom:100%;
					text-align: center;
					padding-bottom: 0.9rem;
				}
				&-scale{
					height: 1.2rem;
					background: url('@{pathToImg}about/history-scale.png') no-repeat center;
					background-size: contain;
				}
			}
		}

	}
	&__info{
		&-list{
			margin-top: 5rem;
			ul.b-list{
				margin-top: 2.5rem;
			}
			&-title{
				font-size: 16/@rem;
				font-weight: 600;
				line-height: 25/@rem;
			}
		}
		&-numerals{
			border: 1px solid @cBorder;
			border-radius: 2/@rem;
			padding: 6rem 4rem 7rem 7rem;
			margin-top: 59/@rem;
			&-inner{
				.clearfix;
			}
			&-item{
				float: left;
				&-text{
					font-size: 14/@rem;
					font-weight: 400;
					line-height: 24/@rem;
					color: @cGray;
					padding-right: 0.5rem;
					&:before{
						content: '';
						width: 7rem;
						height: 2px;
						background-color: @cGold;
						margin-top: 1.2rem;
						margin-bottom: 1.8rem;
						display: block;
					}
				}
				&-val{
					font-size: 60/@rem;
					font-weight: 700;
					line-height: 60/@rem;
					color: @title-color;
					span{
						//font-size: 40/@rem;
					}
				}
				&._1{
					width: 15rem;
				}
				&._2{
					width: 19rem;
				}
				&._3{
					width: 24rem;
				}
				&._4{
					width: 24.4rem;
				}
				&._5{
					width: 23rem;
				}
			}
		}
	}
	&__associations{
		margin-top: 9rem;
		&-title{
			font-size: 20/@rem;
			font-weight: 600;
			line-height: 30/@rem;
			color: @title-color;
			padding-left: 9.4rem;
		}
		&-block{
			.clearfix;
			margin-top: 5.4rem;
		}
		&-img{
			float: left;
			width: 67rem;
			height: 19rem;
			background: url('@{pathToImg}about/associations.png') no-repeat center;
			background-size: contain;
		}
		&-text{
			float: left;
			font-size: 16/@rem;
			line-height: 25/@rem;
			width: 40rem;
			margin-left: 10rem;
			margin-top: 1.4rem;
			position: relative;
			&:before{
				content: '';
				display: block;
				width: 50/@rem;
