					font-size: 1.6rem;
					line-height: 1.6rem;
				}
				&-team{
					padding-top: 2rem;
					padding-bottom: 0.5rem;
					&-item{
						margin-bottom: 4rem;
						&-name{
							.surname{
								font-size: 2.4rem;
								margin-bottom: 1.1rem;
							}
						}
						&-position{
							margin-top: 1rem;
						}
					}
				}
			}
		}
	}
	.about{
		&__contacts{
			padding: 27/@rem 10/@rem 0 30/@rem;
			margin-top: 29/@rem;
			margin-bottom: 8/@rem;
			&-block{
				font-size: 16/@rem;
				line-height: 25/@rem;
				margin-top: 0;
				margin-bottom: 2.6rem;
				max-width: 100rem;
				display: inline-block;
				vertical-align: top;
				margin-right: 2rem;
				&:last-child{
					padding-right: 1rem;
				}
			}
		}
		&__info{
			&-list{
				margin-top: 2rem;
			}
			&-numerals{
				padding: 4rem 2rem 0 3rem;
				margin-top: 2.3rem;
				&-item{
					float: none;
					display: inline-block;
					vertical-align: top;
					margin-bottom: 39/@rem;
					&-text{
						&:before{
							margin-top: 1.9rem;
						}
					}
					&-val{
						font-size: 30/@rem;
						line-height: 24/@rem;
						span{
							//font-size: 40/@rem;
						}
					}
					&._1{
						width: 9.3rem;
					}
					&._2{
						width: 11.9rem;
					}
					&._3{
						width: 10rem;
					}
					&._4{
						width: 17rem;
					}
					&._5{
						width: 14rem;
					}
				}
			}
		}
		&__history{
			padding-bottom: 1rem!important;
			.nav__btn{
				display: none;
				position: absolute;
				top: auto;
				bottom: -24/@rem;
				z-index: 10;
				&._prev{
					left: auto;
					right: 50%;
					margin-right: 0.5rem;
				}
				&._next{
					right: auto;
					left: 50%;
					margin-left: 0.5rem;
				}
			}
			&-scale{
				&-year{
					&:first-child{
						.about__history-scale-year-title{
							left: -1rem;
							text-align: left;
						}
					}
				}
			}
			&-event{
				padding: 3rem;
				&-text{
					font-size: 14/@rem;
					font-weight: 400;
					line-height: 23/@rem;
				}
			}
			&-events{
				&-gall{
					margin-left: 0;
					margin-right: 0;
				}
			}

		}
		&__associations{
			margin-top: 5rem;
			margin-left: -0.5rem;
			margin-right: -0.5rem;
			padding-bottom: 2rem;
			&-title{
				text-align: center;
				padding-left: 0;
				font-size: 18/@rem;
				font-weight: 600;
				line-height: 25/@rem;
			}
			&-block{
				margin-top: 2rem;
			}
			&-img{
				float: none;
				width: 38rem;
				height: 27rem;
				background-image: url('@{pathToImg}about/associations_mob.png');
			}
			&-text{
				float: none;
				font-size: 14/@rem;
				line-height: 20/@rem;
				width: auto;
				margin-left: 5.7rem;
				margin-top: 4.3rem;
				&:before{
					width: 30/@rem;
					height: 30/@rem;
					border-radius:2/@rem;
					left: -5.1rem;
					border-width: 1px;
					top: 0.2rem;
					background-size: 0.7rem 1.8rem;
				}
			}
		}
		&__mission{
			margin-top: 2.4rem;
			padding: 3.7rem 2.5rem 4rem;
			font-size: 14/@rem;
			letter-spacing: 0.05/@rem;
			line-height: 23/@rem;
			h4{
				font-size: 22/@rem;
				line-height: 28/@rem;
				margin-top: 2.7rem;
				margin-bottom: 2.1rem;
			}
		}
		&__principles{
			margin-top: 2.8rem;
			&-blocks{
				margin-top: 2.8rem;
			}
			&-list{
				.clearfix;
				li{
					margin-top: 1rem;
					width: 60%;
					&:nth-child(2n){
						width: 40%;
						padding-right: 0;
					}
				}
			}
			&-block{
				float: none;
				width: 100%;
				p{
					margin-bottom: 2rem;
				}
				&+.about__principles-block{
					margin-left: 0;
					margin-top: 3rem;
				}
			}
		}
		&__controls{
			padding-top: 0;
			font-size: 14/@rem;
			line-height: 20/@rem;
			margin-left: 0;
			margin-right: 0;
			.b-block{
				padding-bottom: 0;
				padding-top: 1.5rem;
			}
			&-team{
				padding-top: 2rem;
				padding-bottom: 4rem;
				&-text{
					margin-bottom: 1.9rem;
					line-height: 23/@rem;
				}
				&-items{

				}
				&-item{
					display: block;
					width: 100%;
					padding-left: 2.7rem;
					margin-bottom: 1rem;
					&:before{
						top: 0.6rem;
					}
					&-name{
						margin-bottom: 0;
						.surname{
							font-size: 1.4rem;
							padding-right: 0;
							margin-bottom: 0;
						}
					}
				}
			}
			&-numerals{
				padding-top: 1rem;
				&-val{
					font-size: 50/@rem;
					letter-spacing: -0.83/@rem;
				}
				&-text{
					&:before{
						margin-top: 1.3rem;
					}
				}
			}
			&-title{
				font-size: 18/@rem;
				line-height: 26/@rem;
			}
			&-block{
				&-content{
					margin-top: 2rem;
					padding-right: 0;
				}
				&-right{
					float: none;
					width: 100%;
					margin-bottom: 33/@rem;
			}
				&-left{
					float: none;
					width: 100%;
				}
				&+.about__controls-block{
					margin-top: 2rem;
				}
			}
		}
		&__shareholders{
			padding-bottom: 0;
			margin-top: 4rem;
			&-table{
				&-wrap{
					margin-right: -2rem;
					margin-left: -2rem;
				}
				.deposit-table__row td{
					font-size: 12/@rem;
					line-height: 20/@rem;
					&:first-child{
						width: 45%;
					}
				}
			}
		}
		&__affiliated{
			padding: 1.5rem -.5rem 4rem;
			.annual-reports .deposit-doc__content .file{
				margin-top: 0;
			}
		}
		&__rating{
			padding-top: 4.2rem;
			padding-bottom: 0.4rem;
			&-title{
				font-size: 45/@rem;
				letter-spacing: -0.75/@rem;
				&:after{
					margin-left: 15/@rem;
				}
			}
			&-text{
				font-size: 23/@rem;
				letter-spacing: -0.33/@rem;
				margin-top: 21/@rem;
			}
			h2{
				text-align: left;
				margin-bottom: 0.6rem;
				&+h3{
					margin-top: 0;
				}
			}
			h3{
				margin-bottom: 2.4rem;
				margin-top: 1.1rem;
			}
			&-items{
				.clearfix;
				&._media{
					.about__rating-item{
						height: 26.6rem;
					}
				}
			}
			.b-list{
				margin-top: 3rem;
				li{
					padding-left: 3rem;
				}
			}
			&-item{
				padding: 2.9rem 2.4rem;
				&._title{
					padding: 0.9rem 0 2.8rem;
					height: auto;
				}
				p{
					&.indent{
						margin-top: 4.3rem;
					}
				}
				width: 100%;
				margin-bottom: 2rem;
				&:nth-child(2n){
					margin-left: 0;
				}
				&-numerals{
					margin-top: 33/@rem;
					&-item{
						&._long{
							width: 70%;
						}
						&-val{
							font-size: 30/@rem;
							letter-spacing: 0;
							line-height: 30/@rem;
						}
						&-text{
							&:before{
								margin-top: 1.6rem;
							}
						}
					}
				}
			}
		}
	}
}