// Reset the box-sizing
//
// Heads up! This reset may cause conflicts with some third-party widgets.
// For recommendations on resolving such conflicts, see
// http://getbootstrap.com/getting-started/#third-box-sizing
* {
	box-sizing:border-box;
}
*:before,
*:after {
	box-sizing:border-box;
}
*,*:hover,*:focus{
	outline: none;
}

// Body reset

html {
	font-size: 10px;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
}

body {
	font-family: @font-family-base;
	font-size: @font-size-base;
	line-height: @line-height-base;
	color: @text-color;
	background-color: #fff;
	font-weight: 400;
	overflow-y: scroll;
	&:after{
		content: '';
		background-color: transparent;
		transition: background-color @animTime 0.05s;
	}
	&.not-found{
		.header{
			box-shadow: 0px 4px 5px 0px rgba(211, 211, 211, 0.2);
		}
		.header__bottom{
			display: none!important;
		}
	}
	&._old-browser{
		background-color: whitesmoke;
		min-width: 990px;
		.header__bottom,.footer,.old-version,.header__top-btn,.header__phones-title{
			display: none!important;
		}
		.grid__inner{
			padding-left: 20px;
			padding-right: 20px;
		}
		.header__phones{
			margin-right: 0;
		}
		.header{
			background-color: white;
		}
	}
}

// Reset fonts for relevant elements
input,
button,
select,
textarea {
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
}


// Links

a {
	color: @cRed;
	text-decoration: none;

	&:hover,
	&:focus {
		color: @cRedHover;
		//text-decoration: @link-hover-decoration;
	}

	&:focus {
		//.tab-focus();
	}
}
.middle{
	white-space: nowrap;
	> * {
		white-space: normal;
	}
	&:after{
		content: '';
		display: inline-block;
		vertical-align: middle;
		width: 0;
		height: 100%;
	}
}
.middle__inner{
	display:inline-block;
	vertical-align:middle!important;
}

.desktop{
	.no-desktop-block, .is-mob-block,.is-tablet-block{
		display: none!important;
	}
}
.tablet{
	.is-mob-block{
		display: none!important;
	}
	&.portrait{
		.is-desktop-block,.no-tablet-block{
			display: none!important;
		}
	}
	&.landscape{
		.is-tablet-block,.no-desktop-block{
			display: none!important;
		}
	}
}
.mobile{
	.no-mob-block, .is-desktop-block, .is-tablet-block{
		display: none!important;
	}
}
.red{
	color: @cRed!important;
}
